html {
  font-family: system-ui, sans-serif;
  font-size: 1.25rem;
  width: 100%;
  height: calc(100% - 50px);
}

body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

.container-fluid {
  margin-top: 50px;
}

.leaflet-container {
  background: #f8f9fa;
}

* {
  outline: none !important;
}

.mapio {
  font-size: 10px;
  font-variant: all-small-caps;
  position: absolute;
  left: 66px;
  top: 32px;
}

.controls {
  position: absolute;
  display: inline-block;
  top: 10px;
  left: 10px;
  background: #f8f9faf0;
  padding: 10px;
  z-index: 999;
  box-shadow: 0px 0px 5px #999;
  overflow: scroll;
  max-height: 500px;
  width: 240px;
}

@media only screen and (max-height: 500px) {
  .controls {
    overflow: scroll;
    height: 200px;
  }

  .timeline {
    position: absolute;
    top: 10px !important;
    right: 10px !important;
    bottom: auto !important;
    left: auto !important;
    background: #f8f9faf0;
    padding: 10px;
    padding-top: 5px;
    z-index: 1000;
    box-shadow: 0px 0px 5px #999;
    text-align: center;
  }

  .leaderboard {
    display: none !important;
  }
}

@media only screen and (max-width: 500px) {
  .leaderboard {
    display: none !important;
  }

  .timeline {
    left: 10px !important;
    right: auto !important;
  }
}

.navbar {
  /*box-shadow: 0px 0px 10px #333;*/
  box-shadow: 0px 0px 5px #999;
}

.controls.minimized {
  width: 51px;
  height: 24px;
  border-radius: 3px;
  padding: 0px;
  z-index: 999;
  box-shadow: 0px 0px 5px #999;
}

.controls.minimized .btn-collapse{
  top: auto;
  left: auto;
  right: auto;
  bottom: auto;
  padding-top: auto !important;
  margin-left: 6px;
  padding: 3px !important;
  width: auto !important;
  text-align: center;
  font-size: 14px !important;
  font-variant: small-caps;
  line-height: normal !important;
  height: auto !important;
}

.timeline {
  position: absolute;
  bottom: 10px;
  right: 392px;
  background: #f8f9faf0;
  padding: 10px;
  padding-top: 5px;
  z-index: 10000;
  box-shadow: 0px 0px 5px #999;
  text-align: center;
}

.showInMomentum {
  display: none;
}

.btn-collapse {
  position: absolute;
  right: 10px;
  top: 12px;
  font-size: 10px !important;
  line-height: 12px !important;
  height: 12px !important;
  outline: none !important;
  cursor: pointer;
  color: #337ab7 !important;
  background: none;
  border: none;
}

.btn-collapse:hover {
  color: #000 !important;
}

.leftTime, .midTime, .todayTime, .play, .stop, .pause {
  margin-left: 5px;
}

#leaflet-map, .leaflet-container {
  width: 100%!important;
  height: 100%!important;
}

.credits {
  margin-top: 10px;
  text-align: right;
}

.credits .badge {
  font-size: 10px;
  font-weight: 300 !important;
}

