.locationSelect {
  cursor: pointer;
}

.locationSelect:HOVER {
  background: #fff;
}

.locationSelect:HOVER td {
  opacity: 0.75;
}

.locationSelect:HOVER .country {
  color: black;
  text-decoration: underline;
}

.mono {
  font-family: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  font-size: 11px;
}

.stat {
  width: 50px !important;
  min-width: 50px !important;
  max-width: 50px !important;
  font-family: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  font-size: 11px;
}

.valign-top {
  vertical-align: top;
}

.sortHeader {
  font-weight: bold;
  cursor: pointer;
}

g.todayMarker {
  fill: #ccc;
}

g.confirmedProjectedBar {
  fill: #0000FFcc;
}

g.confirmedBar {
  fill: #FF0000cc;
}

g.recoveredBar {
  fill: #00FF00cc;
}

g.deceased {
  fill: #000000cc;
}
